import React from "react";
import { render } from "react-dom";
import Hello from "./Hello";
import Pallo from "./Pallo/Pallo";
import Symbol from "./Symbol";

const styles = {
  fontFamily: "sans-serif",
  textAlign: "center"
};

class Main extends React.Component {
  onClickHandler() {
    console.log("button clicked" + this.pallo);

    console.log("Before heippa2 call");
    var str = this.refs.hola.heippa2("Rico"); //,"Laku");

    console.log("After heippa2 call");

    // ei pakauta koska async mitään muuta kuin promisen
    console.log("return value from pallo= " + str);
    // mutta seuraava synkroninen kutusu palauttaa parametrina saadun
    var val = this.refs.hola.nonAsyncFunc("Rico");
    console.log("return value from pallo nonAsynFunc= " + val);
  }

  onClickHandler2() {
    console.log("Clicked 2");
    this.refs.hola.prog();
  }

  palloFunc(param) {
    console.log("CallBack succeeded " + param);
  }

  render() {
    return (
      <div>
        <button onClick={this.onClickHandler.bind(this)}>Lick My Ass</button>
        <button onClick={this.onClickHandler2.bind(this)}>Kiss My Ass</button>
        <Pallo
          bgColor="green"
          callBack={this.palloFunc.bind(this)}
          ref="hola"
        />
        {/*ref={hola=>{this.hola=hola;}}*/}
      </div>
    );
  }
}
// tätä ei käytetä... vaan main
const App = () => (
  <div style={styles}>
    <Hello name="CodeSandbox" />
    <Pallo bgColor="green" />
    <Pallo bgColor="red" />

    {/*<svg>
      <circle cx={50} cy={50} r={10} fill="red" />
    </svg>*/}

    <h2>Start editing to see some majjjjjjgic happen {"\u2728"}</h2>
  </div>

  //pallo.heippa();
);
//render(<App />, document.getElementById("root"));
render(<Main />, document.getElementById("root"));



