import React from "react";

export default class Pallo extends React.Component {
  //var col="red";
  constructor(props) {
    super(props);
    console.log("constructor param=" + this.props.bgColor);
    this.state = {
      bgColor: this.props.bgColor,
      timerID: "",
      timerStart: false
    };
  }

  omponentDidMount() {
    console.log("DidMount");
    //this.timerID = setInterval(() => this.tick(), 1000);
  }

  componentWillUnmount() {
    //clearInterval(this.timerID);
  }

  tick() {
    console.log("timer evaluate");
  }

  heippa = (param, param2) => {
    console.log("heippa onClick");
    this.setState({
      bgColor: "red"
    });

    for (var i = 0; i < 100; i++) {
      console.log("Pallo in heippa i= " + i);
    }

    return param + "  " + param2;
  };

  // omaan utils luokkaan
  timeoutPromise(timeout) {
    return new Promise(resolve => setTimeout(resolve, timeout));
  }

  prog = async param => {
    console.log("Start Timer");
    this.setState({ timerStart: true });
    /* this.timerID = setInterval(() => {
      console.log("timer running");
    }, 1000);
*/
    //this.timerID = setInterval(() => this.seq2.bind(this), 1);
    this.timerID = setInterval(this.seq2.bind(this), 1000);
    //console.log("timerID= " + this.timerID + "   " + this.seq2);
    /*this.setState({
      timerID: setInterval(() => this.seq2, 1)
    });*/
  };

  seq2() {
    console.log("In seq call heippa2");
    // await this.heippa2();
    console.log("Stop Timer");
    clearInterval(this.timerID);
  }

  seq = async param => {
    console.log("In seq call heippa2");
    // await this.heippa2();
    console.log("Stop Timer");
    clearInterval(this.timerID);
  };

  heippa2 = async param => {
    console.log("heippa onClick");
    this.setState({
      bgColor: "red"
    });

    console.log("Before loop");
    for (var i = 0; i < 100; i++) {
      await this.timeoutPromise(1);
      console.log("Pallo in heioppa2 i= " + i);
    }
    console.log("After loop");
    console.log("Before callBack");
    this.props.callBack("Ok");
    console.log("After callBack");

    // ei palauta mitä haluatnn koska fukito on async
    return param;
  };

  async onClickHandler() {
    //event.preventDefault();

    this.setState({
      bgColor: "blue"
    });

    await this.prog();
    //circ.style.fill="yellow";

    console.log("heippa pallo");
    //this.heippa();
  }
  render() {
    console.log("render");
    /*
    if (this.state.timerStart) {
      //this.timerID = setInterval(() => this.seq2.bind(this), 1);
      this.timerID = setInterval(() => {
        console.log("timer running");
      }, 1000);
      console.log("xxxxxxxxxxxxxxxxxxxx");
    }*/

    return (
      <svg>
        <circle
          onClick={this.onClickHandler.bind(this)}
          id="circ"
          cx={50}
          cy={50}
          r={10}
          style={{
            fill: this.state.bgColor,
            fillOpacity: "0.6",
            stroke: "blue",
            strokeWidth: "2"
          }}
        />
      </svg>
    );
  }
}



